// HTTP   

/**
 *   
 * @param {*} data -  
 * @param {number} statusCode - HTTP   (: 200)
 * @param {Object} headers -  
 * @returns {Object} Lambda  
 */
function success(data, statusCode = 200, headers = {}) {
  return {
    statusCode,
    headers: {
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Headers': 'Content-Type,X-Amz-Date,Authorization,X-Api-Key,X-Amz-Security-Token',
      'Access-Control-Allow-Methods': 'GET,POST,PUT,DELETE,OPTIONS',
      ...headers,
    },
    body: JSON.stringify({
      success: true,
      data,
      timestamp: new Date().toISOString(),
    }),
  };
}

/**
 *   
 * @param {string} message -  
 * @param {number} statusCode - HTTP   (: 500)
 * @param {string} errorCode -  
 * @param {Object} headers -  
 * @returns {Object} Lambda  
 */
function error(message, statusCode = 500, errorCode = 'INTERNAL_ERROR', headers = {}) {
  return {
    statusCode,
    headers: {
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Headers': 'Content-Type,X-Amz-Date,Authorization,X-Api-Key,X-Amz-Security-Token',
      'Access-Control-Allow-Methods': 'GET,POST,PUT,DELETE,OPTIONS',
      ...headers,
    },
    body: JSON.stringify({
      success: false,
      error: {
        message,
        code: errorCode,
        statusCode,
      },
      timestamp: new Date().toISOString(),
    }),
  };
}

/**
 *     
 * @param {string} message -  
 * @param {Array} details -   
 * @returns {Object} Lambda  
 */
function validationError(message, details = []) {
  return error(message, 400, 'VALIDATION_ERROR', {}, { details });
}

/**
 * 404 Not Found  
 * @param {string} resource -    
 * @returns {Object} Lambda  
 */
function notFound(resource = 'Resource') {
  return error(`${resource} not found`, 404, 'NOT_FOUND');
}

/**
 * 401 Unauthorized  
 * @param {string} message -  
 * @returns {Object} Lambda  
 */
function unauthorized(message = 'Unauthorized') {
  return error(message, 401, 'UNAUTHORIZED');
}

/**
 * 403 Forbidden  
 * @param {string} message -  
 * @returns {Object} Lambda  
 */
function forbidden(message = 'Forbidden') {
  return error(message, 403, 'FORBIDDEN');
}

/**
 * 429 Too Many Requests  
 * @param {string} message -  
 * @returns {Object} Lambda  
 */
function tooManyRequests(message = 'Too many requests') {
  return error(message, 429, 'TOO_MANY_REQUESTS');
}

module.exports = {
  success,
  error,
  validationError,
  notFound,
  unauthorized,
  forbidden,
  tooManyRequests,
};