//  

const environment = require('../config/environment');

/**
 *   
 */
const LOG_LEVELS = {
  ERROR: 0,
  WARN: 1,
  INFO: 2,
  DEBUG: 3,
};

/**
 *    
 */
const currentLogLevel = environment.isDevelopment ? LOG_LEVELS.DEBUG : LOG_LEVELS.INFO;

/**
 *   
 * @param {string} level -  
 * @param {string} message - 
 * @param {Object} meta -  
 * @returns {Object}   
 */
function formatLog(level, message, meta = {}) {
  return {
    timestamp: new Date().toISOString(),
    level,
    message,
    stage: environment.stage,
    ...meta,
  };
}

/**
 *   
 * @param {number} level -   
 * @param {string} levelName -   
 * @param {string} message - 
 * @param {Object} meta -  
 */
function log(level, levelName, message, meta = {}) {
  if (level <= currentLogLevel) {
    const logData = formatLog(levelName, message, meta);
    
    if (level === LOG_LEVELS.ERROR) {
      console.error(JSON.stringify(logData));
    } else if (level === LOG_LEVELS.WARN) {
      console.warn(JSON.stringify(logData));
    } else {
      console.log(JSON.stringify(logData));
    }
  }
}

/**
 *  
 * @param {string} message - 
 * @param {Error|Object} error -  
 * @param {Object} meta -  
 */
function error(message, error = null, meta = {}) {
  const errorMeta = {
    ...meta,
    error: error ? {
      name: error.name,
      message: error.message,
      stack: error.stack,
    } : null,
  };
  
  log(LOG_LEVELS.ERROR, 'ERROR', message, errorMeta);
}

/**
 *  
 * @param {string} message - 
 * @param {Object} meta -  
 */
function warn(message, meta = {}) {
  log(LOG_LEVELS.WARN, 'WARN', message, meta);
}

/**
 *  
 * @param {string} message - 
 * @param {Object} meta -  
 */
function info(message, meta = {}) {
  log(LOG_LEVELS.INFO, 'INFO', message, meta);
}

/**
 *  
 * @param {string} message - 
 * @param {Object} meta -  
 */
function debug(message, meta = {}) {
  log(LOG_LEVELS.DEBUG, 'DEBUG', message, meta);
}

/**
 * API  
 * @param {Object} event - Lambda  
 * @param {string} functionName - 
 */
function logRequest(event, functionName) {
  info('API Request', {
    function: functionName,
    httpMethod: event.httpMethod,
    path: event.path,
    queryStringParameters: event.queryStringParameters,
    headers: {
      'user-agent': event.headers?.['user-agent'],
      'x-forwarded-for': event.headers?.['x-forwarded-for'],
    },
  });
}

/**
 * API  
 * @param {Object} response - Lambda  
 * @param {string} functionName - 
 * @param {number} duration -   (ms)
 */
function logResponse(response, functionName, duration) {
  info('API Response', {
    function: functionName,
    statusCode: response.statusCode,
    duration: `${duration}ms`,
  });
}

module.exports = {
  error,
  warn,
  info,
  debug,
  logRequest,
  logResponse,
  LOG_LEVELS,
};